@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&display=swap');
:root {
  /* colors */
  --clr-soft-orange: 35, 77%, 62%;
  --clr-soft-red: 5, 85%, 63%;
  --clr-off-white: 36, 100%, 99%;
  --clr-gray-blue: 233, 8%, 79%;
  --clr-dark-gray-blue: 236, 13%, 42%;
  --clr-deep-dark-blue: 240, 100%, 5%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*               */
/*    RESET      */
/*               */
body,
h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}
body {
  color: hsl(var(--clr-dark-gray-blue));
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 0.9375rem;
  background-color: hsl(var(--clr-off-white));
}
.flow>*:where(:not(:first-child)) {
  margin-top: var(--flow-space, 1rem);
}
/*        */
/* HEADER */
/*        */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 5rem;
  padding-bottom: 3rem;
  max-width: 72rem;
  margin: 0 auto;
}
.navbar {
  display: flex;
  list-style: none;
  text-decoration: none;
  cursor: pointer;
}
.navbar>* {
  padding-left: 2rem;
}
.navbar a{
  transition: 500ms;
}
.logo {
  width: 4rem;
}
.toggleNavbar {
  display: none;
}
.overlay {
  display: none;

  inset: 0 0 0 0;
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: -2000;
}
@media (max-width: 45rem) {
  .header {
    padding: 1.5rem 1rem;
  }
  .navbar {
    gap: 1rem;
    position: fixed;
    flex-direction: column;
    inset: 0 0 0 35%;
    z-index: 1900;

    padding: min(20rem, 25vh) 0.75rem;
    margin: 0;

    transform: translateX(100%);
    transition: all 500ms ease-in-out;
    background-color: hsl(var(--clr-off-white));
    color: hsl(var(--clr-deep-dark-blue));
  }
  .navbar.active {
    transform: translateX(0);
  }
  .overlay {
    display: block;
    position: fixed;
    background-color: hsl(var(--clr-deep-dark-blue));
    opacity: 0;
    z-index: 1800;
    transition: all 500ms ease-in-out;
  }
  .overlay.active {
    opacity: 0.5;
  }
  .toggleNavbar {
    display: block;
    position: absolute;
    z-index: 2000;
    right: 1rem;
    top: 1.5rem;
    background: transparent;
    background-repeat: no-repeat;
    background-position: top;
    width: 2.5rem;
    aspect-ratio: 1;
    border: 0;
  }
  .logo {
    width: 2.5rem;
  }
}
/*               */
/*     HERO      */
/*               */
.hero {
  display: grid;
  
  grid-template-columns: minmax(1rem, 1fr) minmax(0, 72rem) minmax(1rem, 1fr);
  grid-template-areas:
  '. image .'
  '. title  .'
  '. content .'
  '. button . '
  '. newsbar .';
  
  padding-bottom: 3rem;
}
.hero h1 {
  grid-area: title;
  font-weight: 800;
  font-size: 2.5rem;
  color: hsl(var(--clr-deep-dark-blue));
  max-width: 20rem;
}
.hero-img {
  grid-area: image;
  width: 100%;
}
.hero-img img{
  width: 100%;
}
.hero p{
  grid-area: content;
}
.read-more-btn {
  cursor: pointer;
  grid-area: button;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  font-size: 0.7rem;
  letter-spacing: 0.2rem;
  text-transform: uppercase;

  color: hsl(var(--clr-off-white));
  background-color: hsl(var(--clr-soft-red));

  border: 0;
  align-self: end;
  margin-right: auto;
  padding: 1rem 2rem;

  transition: 500ms;
}
/*               */
/*   NEWS BAR    */
/*               */
.news-bar{
  --flow-space: 2rem;
  grid-area: newsbar;
  background-color: hsl(var(--clr-deep-dark-blue));
  padding: 1rem 1rem 0 1rem;
}
.news-bar h2{
  font-size: 2rem;
  color: hsl(var(--clr-soft-orange))
}
.news-bar h3{
  font-size: 1.25rem;
  color: hsl(var(--clr-off-white));

  transition: 500ms;
}
.news-bar p{
  margin-top: 0.5rem;
  line-height: 1.5;
  padding-bottom: 2rem;
  color: hsl(var(--clr-gray-blue));
}
.news-bar > .topic:where( :not(:last-child)) {
  border-bottom: 1.5px solid hsl(var(--clr-dark-gray-blue));
}
/*               */
/*      CARD     */
/*               */
.card-panel {
  display: flex;
  flex-direction: column;
  max-width: 72rem;
  padding: 0 1rem;
  max-width: 72rem;
  margin: 0 auto 3rem auto;
}
.card{
  width: 100%;
  display: grid;
  grid-template-areas:
    'image number'
    'image heading'
    'image content';
  column-gap: 1.5rem;
  margin-bottom: 1.5rem;
}
.card h2{
  grid-area: number;
  font-size: 1.75rem;
  color: hsl(var(--clr-gray-blue));
}
.card h3{
  grid-area: heading;
  color: hsl(var(--clr-deep-dark-blue));
  font-weight: 800;
  
  transition: 500ms;
}
.card p{
  grid-area: content;
  line-height: 1.5;
}
.card img{
  grid-area: image;
  width: 6rem;
}
@media (min-width: 45rem) {
  /*               */
  /*     HERO      */
  /*               */
  .hero {
    grid-template-columns: minmax(1rem, 1fr) repeat(3, minmax(0, 23rem)) minmax(1rem, 1fr);
    column-gap: 1.5rem;
    grid-template-areas:
    '. image image newsbar.'
    '. title content newsbar.'
    '. title button newsbar.';
  }
  .hero h1 {
    font-size: 3.5rem;
  }
  .hero p{
    font-size: 1rem;
    line-height: 1.5;
  }
  .read-more-btn{
    font-size: 1rem;
    letter-spacing: .3rem;
  }
  /*               */
  /*   NEWS BAR    */
  /*               */
  .news-bar{
    padding: 2rem 1.5rem 0 1.5rem;
    margin-top: 0;
  }
  .news-bar h2{
    font-size: 2.5rem;
  }
  .news-bar h3{
    font-size: 1.25rem;
  }
  /*               */
  /*      CARD     */
  /*               */
  .card-panel {
    padding: 0;
    flex-direction: row;
    gap: 1.5rem;
  }
  /*               */
  /*     HOVERS    */
  /*               */
  .navbar a:hover{
    color: hsl(var(--clr-soft-red));
  }
  .read-more-btn:hover{
  background-color: hsl(var(--clr-deep-dark-blue));
  }
  .news-bar h3:hover{
    cursor: pointer;
    color: hsl(var(--clr-soft-orange));
  }
  .card h3:hover{
    cursor: pointer;
    color: hsl(var(--clr-soft-red));
  }
}